.trees-container{
    display: flex;
    position: relative;
    margin: 0 5vw 0 5vw
}

.tree-base{
    width:25px;
    height:50px;
    background-color: black;
}

.tree-base::before{
    content: '';
    width: 0;
    height: 0;
    border-left: 40px solid transparent;
    border-right: 40px solid transparent;
    border-bottom: 100px solid black;
    position: absolute;
    left: -25px;
    bottom: 40px;
}

.tree-base::after{
    content: '';
    width: 0;
    height: 0;
    border-left: 40px solid transparent;
    border-right: 40px solid transparent;
    border-bottom: 100px solid black;
    position: absolute;
    left: -25px;
    bottom: 80px;
}

@media screen and (max-width: 500px) {
    .tree-base{
        width: 12.5px;
        height: 25px;
    }

    .tree-base::before{
        border-left: 20px solid transparent;
        border-right: 20px solid transparent;
        border-bottom: 50px solid black;
        left: -12.5px;
        bottom: 20px;
    }

    .tree-base::after{
        border-left: 20px solid transparent;
        border-right: 20px solid transparent;
        border-bottom: 50px solid black;
        left: -12.5px;
        bottom: 40px;
    }
}