.hero-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: white;
}

.hero-container .presentation{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.hero-container .presentation h2 {
    margin: 0;
}

.hero-container h2 {
    font-family: Verdana;
}

.hero-container .presentation h1 {
    margin: 0;
    font-size: 60px;
    font-family: Verdana;
}

.hero-container p {
    font-size: 50px;
    z-index: 1;
    font-family:fantasy;
}


@media (prefers-reduced-motion: no-preference) {
    .hero-container p{
        animation: pressed-enter-fade infinite 1s;
    }
    .hero-container .letsgooo{
        animation: blink-letsgooo infinite 0.5s;
    }
}

@keyframes pressed-enter-fade {
    50%{
        opacity: 0;
    }
}

@keyframes blink-letsgooo {
    49%{
        opacity: 1;
    }
    50%{
        opacity: 0;
    }
    99%{
        opacity: 0;
    }
}